import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
import "./index.scss"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faReact} from '@fortawesome/free-brands-svg-icons'
import {faSmileWink, faBiking} from '@fortawesome/free-solid-svg-icons'
import { faGithub } from '@fortawesome/free-brands-svg-icons'

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
  
    <div style={{ 
      maxWidth: `100%`, 
      marginBottom: `1.45rem` 
    }}>
      <Image name="monster.png" alt="Nova Scotia"/>
    </div>

    <p class="mt-3">I was a mentor and “developer”.</p>

    <p>
    I dreamed of splitting my legs in yoga class, 
    riding my Megavalanche mountain bike, and teaching React class.
    </p>

    <div class="mt-3 mb-3">
      <div class="row">
        <div class="col-sm-12 col-md-4 pt-3 d-flex justify-content-center">
        <FontAwesomeIcon icon={faSmileWink} size="5x" style={{
          color: `#2a8ef4`
        }} />
        </div>
        <div class="col-sm-12 col-md-4 pt-3 d-flex justify-content-center">
        <FontAwesomeIcon icon={faBiking} size="5x" style={{
          color: `#deac78`,
          transform: `rotate(15deg)`,
          marginTop: `-10px`
        }} />
        </div>
        <div class="col-sm-12 col-md-4 pt-3 d-flex justify-content-center">
        <FontAwesomeIcon icon={faReact} size="5x" style={{
          color: `#b259b4`
        }} />
        </div>
      </div>
    </div>

    <h3 class="mt-5 mb-2">Fun Fact</h3>
    <ul class="mb-5">
      <li>Hiking</li>
      <li>Simple nature expression</li>
      <li>Ladybug squad</li>
    </ul>
    <ul class="mr-0 pl-0">
    <li class="d-sm-block d-md-inline mr-3">
        <Link to="/portfolio">
          Portfolio
        </Link>
      </li>
      <li class="d-sm-block d-md-inline mr-3">
        <Link to="/experiences">
          Experiences
        </Link>
      </li>
      <li class="d-sm-block d-md-inline mr-3">
        <Link to="/learning">
          Learning
        </Link>
      </li>
      <li class="d-sm-block d-md-inline mr-3">
        <Link to="/motivation">
          Motivation
        </Link>
      </li>
      <li class="d-sm-block d-md-inline mr-3">
        <Link to="/sharing">
          Sharing
        </Link>
      </li>
      <li class="d-sm-block d-md-inline mr-3">
        <a href="https://github.com/ccapeng/"
          target="_blank" 
          title="Github"
          rel="noreferrer">
            <FontAwesomeIcon icon={faGithub} size="1x"/>
        </a>
      </li>
    </ul>

  </Layout>
)

export default IndexPage